'use strict';

var zhData = require('./lang/zh.js');

/**
 * 
 */
var S = zhData.S;

/**
 * 
 */
var T = zhData.T;

/**
 * 
 * @param {String} str - 
 * @param {Boolean} toT - 
 * @returns {String} - 
 */

function tranStr (str, toT) {
    var i;
    var letter;
    var code;
    var isChinese;
    var index;
    var src;
    var des;
    var result = '';

    if (toT) {
        src = S;
        des = T;
    } else {
        src = T;
        des = S;
    }

    if (typeof str !== 'string') {
        return str;
    }

    for (i = 0; i < str.length; i++) {
        letter = str.charAt(i);
        code = str.charCodeAt(i);
        // Unicode
        isChinese = (code > 0x3400 && code < 0x9FC3) || (code > 0xF900 && code < 0xFA6A);
        if (!isChinese) {
            result += letter;
            continue;
        }

        index = src.indexOf(letter);

        if (index !== -1) {
            result += des.charAt(index);
        } else {
            result += letter;
        }
    }

    return result;
}


var Chinese = {
    s2t:function (str){
		return tranStr(str,true)
	},
	t2s:function (str){
		return tranStr(str,false)
	}
}

module.exports = Chinese;